import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { Vacation } from '../models/vacation';
import { Employee } from '../models/employee';


@Injectable({ providedIn: 'root' })
export class VacationService {
    constructor(private http: HttpClient) { }

    getAll() {
        return this.http.get<Vacation[]>(`${environment.apiUrl}/vacation/vacations`);
    }

    getByRegistration(registration: string){
        return this.http.get<Vacation[]>(`${environment.apiUrl}/vacation/vacationByRegistration?registration=${registration}`);
    }

    createNewVacation(startDate: Date, finishDate: Date, employees: Employee) {

        const headers = new HttpHeaders(
            {
                'Content-Type': 'application/json'
            }
        );
        const options = { headers };

        const body: Vacation = {

            startDate: startDate,
            finishDate: finishDate,
            employees: employees
        };

        return this.http.post<any>(`${environment.apiUrl}/vacation/createVacation`, body, options)
            .pipe(map(vacation => {
                return vacation;
            }));
    }

    updateTeam(vacation: Vacation) {
        const headers = new HttpHeaders(
            {
                'Content-Type': 'application/json'
            }
        );
        const options = { headers };

        return this.http.put<any>(`${environment.apiUrl}/vacation/updateVacation/` + vacation.id, vacation, options)
            .pipe(map(t => {
                return t;
            }));
    }
}