import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Employee } from '../models/employee';


@Injectable({ providedIn: 'root' })
export class EmployeeService {
    constructor(private http: HttpClient) { }

    getAll() {
        return this.http.get<Employee[]>(`${environment.apiUrl}/employee/employees`);
    }

    getByRegistration(registration: string) {
        return this.http.get<Employee[]>(`${environment.apiUrl}/employee/employeeByRegistration?registration=${registration}`);
    }

    getByName(name: string) {
        return this.http.get<Employee[]>(`${environment.apiUrl}/employee/employeeByName?name=${name}`);
    }
}