import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { VacationService } from '../services/vacation.service';
import { Vacation } from '../models/vacation';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Observable, of } from 'rxjs';
import { Employee } from '../models/employee';
import { EmployeeComponent } from '../employee/employee.component';
import { EmployeeService } from '../services/employee.service';
import { startWith, debounceTime, switchMap, map, catchError } from 'rxjs/operators';

@Component({
  selector: 'app-new-vacation',
  templateUrl: './new-vacation.component.html'
})
export class NewVacationComponent implements OnInit {

  vacationForm: FormGroup;
  employeeOptions: Observable<Employee> = null;
  loading = false;
  teamInvalid = false;
  teamToUpdate: Vacation;

  constructor(private formBuilder: FormBuilder,
    private vacationService: VacationService,
    private employeeService: EmployeeService,
    public dialogRef: MatDialogRef<NewVacationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Vacation) { }

  ngOnInit(): void {
    this.vacationForm = this.formBuilder.group({
      startDate: new FormControl(new Date()),
      finishDate: new FormControl(new Date()),
      autoComplete: ['', Validators.required]
    });

    this.employeeOptions = this.vacationForm.controls['autoComplete'].valueChanges.pipe(
      startWith(''),
      // delay emits
      debounceTime(300),
      // use switch map so as to cancel previous subscribed events, before creating new once
      switchMap(value => {
        if (value !== '') {
          // lookup from github
          return this.lookup(value);
        } else {
          // if no value is present, return null
          return of(null);
        }
      })
    )

  }

  lookup(value: string): Observable<Employee[]> {
    return this.employeeService.getByName(value.toString().toLowerCase()).pipe(
      // map the item property of the github results as our return object
      map(results => results),
      // catch errors
      catchError(_ => {
        return of(null);
      })
    );
  }

  saveVacation(vacationFormValue) {
    if (this.vacationForm.valid) {

      this.vacationService.createNewVacation(vacationFormValue.startDate, vacationFormValue.finishDate, vacationFormValue.autoComplete)
        .subscribe(data => {
          this.closeDialog();
        }, error => {
          console.error(error);
        });
    }
  }

  closeDialog() {
    this.dialogRef.close();
  }

  onClear() {
    // tslint:disable-next-line: no-unused-expression
    this.vacationForm.reset;
  }

  getOptionText(option) {
    if (option) {
      return option.name;
    }
    return '';
  }

}
