import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Vacation } from '../models/vacation';
import { VacationService } from '../services/vacation.service';
import { FormControl } from '@angular/forms';
import { EmployeeService } from '../services/employee.service';
import { Observable } from 'rxjs';
import { NewVacationComponent } from '../new-vacation/new-vacation.component';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-vacation',
  templateUrl: './vacation.component.html'
})
export class VacationComponent implements OnInit {

  constructor(private vacationService: VacationService,
              private employeeServie: EmployeeService,
              public dialog: MatDialog,
              private changeDetectorRefs: ChangeDetectorRef) { }

  displayedColumns: string[] = ['id', 'startDate', 'finishDate'];
  dataSource = [];
  formControl = new FormControl();
  filteredOptions: Observable<string[]>;

  ngOnInit(): void {
  }

  searchVacationByRegistration(){
    this.vacationService.getByRegistration(this.formControl.value)
    .subscribe(
      data => {
          this.dataSource = data;
          this.changeDetectorRefs.detectChanges();
      },
      error => {
          console.error(error);
          this.dataSource = [];
          this.changeDetectorRefs.detectChanges();
      });
  }

  getAll() {
    this.vacationService.getAll()
    .subscribe(
        data => {
            this.dataSource = data;
            this.changeDetectorRefs.detectChanges();
        },
        error => {
            console.error(error);
            this.changeDetectorRefs.detectChanges();
        });
  }

  searchEmployees(){
    this.employeeServie.getByRegistration(this.formControl.value)
    .subscribe(
      data => {
          this.dataSource = data;
      },
      error => {
          console.error(error);
      });
  }

  showNewVacationDialog(): void {
    const dialogRef = this.dialog.open(NewVacationComponent, {
      width: '750px'
    });
  }

  showUpdateVacationDialog(vacation: Vacation): void {

  }

}
