import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Team } from '../models/team';
import { map } from 'rxjs/operators';


@Injectable({ providedIn: 'root' })
export class TeamService {
    constructor(private http: HttpClient) { }

    getAll() {
        return this.http.get<Team[]>(`${environment.apiUrl}/team/teams`);
    }

    createNewTeam(teamName: string) {

        const headers = new HttpHeaders(
            {
                'Content-Type': 'application/json'
            }
        );
        const options = { headers};

        const body: Team = {
            name : teamName
        };

        return this.http.post<any>(`${environment.apiUrl}/team/createTeam`, body, options)
            .pipe(map(team => {
                return team;
            }));
    }

    updateTeam(team: Team) {
        const headers = new HttpHeaders(
            {
                'Content-Type': 'application/json'
            }
        );
        const options = { headers};

        return this.http.put<any>(`${environment.apiUrl}/team/updateTeam/` + team.id, team, options)
            .pipe(map(t => {
                return t;
            }));
    }
}