import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TeamService } from '../services/team.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Team } from '../models/team';

@Component({
  selector: 'app-team-dialog',
  templateUrl: './team-dialog.component.html'
})
export class TeamDialogComponent implements OnInit {

  teamForm: FormGroup;
  loading = false;
  teamInvalid = false;
  teamToUpdate: Team;

  constructor(private formBuilder: FormBuilder,
              private teamService: TeamService,
              public dialogRef: MatDialogRef<TeamDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: Team) { }

  ngOnInit(): void {
    this.teamForm = this.formBuilder.group({
      name: ['', Validators.required]
    });
    this.teamToUpdate = this.data;
    this.f.name.setValue(this.data.name);
  }

  // convenience getter for easy access to form fields
  get f() { return this.teamForm.controls; }

  onSubmit() {

    // stop here if form is invalid
    if (this.teamForm.invalid) {
        console.error('Login invÃ¡lido');
        return;
    }

    if(this.teamToUpdate != null){
      this.onUpdate();
      return;
    }

    this.loading = true;
    this.teamService.createNewTeam(this.f.name.value)
          .subscribe(
              data => {
              },
              error => {
                  console.error(error);
                  this.teamInvalid = true;
                  this.loading = false;
              });
  }

  onUpdate() {
    this.teamToUpdate.name = this.f.name.value;
    this.teamService.updateTeam(this.teamToUpdate)
    .subscribe(
        data => {
          this.closeDialog();
        },
        error => {
            console.error(error);
            this.teamInvalid = true;
            this.loading = false;
        });
  }

  closeDialog() {
    this.dialogRef.close();
  }

  onClear(){
    // tslint:disable-next-line: no-unused-expression
    this.teamForm.reset;
  }

}
